body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.shareSheetContainer{display:flex;flex-direction:column;align-items:center;justify-content:center;flex-wrap:wrap;position:fixed;height:188px;width:42px;z-index:1;margin-left:11%;bottom:82px}.shareButton{margin:5px}.copyIconDiv{display:flex;justify-content:center;align-items:center;background:#7F7F7F;color:white;border-radius:50%;height:100%;width:100%}.copyLink{height:31px;width:31px;margin:5px}

.placeholderContainer{display:flex;justify-content:center;height:100vh}.placeholder{position:fixed;max-height:78vh;max-width:100vw}@media only screen and (max-width: 600px){.placeholder{top:5vh}}.copyConfirmationContainer{position:fixed;width:100%;display:flex;justify-content:center;top:26vh;z-index:1}.copyConfirmation{display:flex;justify-content:center;align-items:center;box-shadow:3px 19px 46px;border-radius:16px;height:25px;width:70px;padding:5px;top:100px;background:white;box-shadow:4px 4px 6px gray;font-family:"Montserrat", sans-serif}#homeContainer{height:100vh}.editorDiv{display:flex;justify-content:center}

.logoContainer{display:flex;justify-content:flex-start}.logo{max-width:70vw;max-height:30vh}.aboutContainer{display:flex;justify-content:center;font-weight:400;font-size:22px;font-family:"Montserrat", sans-serif}.aboutContent{height:90vh;display:flex;flex-direction:column;margin-top:5vh;padding:0 30px 0 30px}.aboutParagraph,.authors{padding-left:25px}.authors{font-size:18px}a:link{text-decoration:none}

body{overflow:auto;font-family:"Montserrat", sans-serif}.editorContainer{display:flex;justify-content:center;position:absolute;top:0}textarea{font-size:large;font-family:'Montserrat';resize:none;margin-top:40px;border:none;overflow:auto;outline:none;box-shadow:none;resize:none;height:95vh;width:65vw;padding:0 16vw 0}@media only screen and (max-width: 600px){textarea{width:78vw;padding:0 30px 0}}@media screen and (min-device-width: 600px) and (max-device-width: 1024px){textarea{width:60vw;padding:0 165px 0}}

